.header {
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 1px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.header .logo {
    font-family: "Playfair Display", serif;
    font-size: 2.5rem;
}

.shopping-cart {
    font-size: 1.3rem;
}

.cart-icon {
    font-size: 1.5rem;
    color: cadetblue;
    margin-right: 10px;
}

